<template>
<el-main>
    <el-card>
        <el-form ref="form"  label-width="50px">
        <el-form-item label="标题">
            <el-input 
            :autosize="autosize" 
            v-model="newInfo.Title"></el-input>
        </el-form-item>
        <el-form-item label="内容">
            <el-input type="textarea" 
            v-model="newInfo.Content"
            :autosize="autosize" ></el-input>
        </el-form-item>
        <el-form-item>
            <el-button 
            type="primary"  
            @click="UpdateInfo">update</el-button>
        </el-form-item>
        </el-form>
    </el-card>
</el-main>


</template>

<script>
export default {
props:{
    newInfo:{type:Object,required:true}
},
data(){
    return{
        autosize:true,
        
    }
},
methods:{
    UpdateInfo(){   
        this.$emit('updateFormChild' , this.newInfo)
        
    }
}
}
</script>

