<template>
<el-main  >
    <NewInfo
    @updateFormChild="addNewInfo"
    :newInfo="newInfo"></NewInfo>
    <SingleInfo v-for="(i,index) in load.Data" 
    :key="index"  
    :title="i.Title"
    :updateusr="i.Username"
    :updateTime="i.StartTime"
    :content="i.Content"
    :index="index"
    @AddbyChild = "AddtoMyTodo"
    @FixByChild = "Fix"
    @RemoveByChild="Remove"></SingleInfo>
    <FixInfo
    :name="name"
    v-on:closeByChild="closeByChild"
    :dialogFormVisible="dialogFormVisible"
    :form='fixForm'
    ></FixInfo>
</el-main>
</template>

<script>
import FixInfo from "../components/Info/FixInfo"
import SingleInfo from "../components/Info/SingleInfo"
import {SearchInfo,AxiosDelete, AxiosGet , InfoAdd,InfoTodo} from "../../util/submit"// eslint-disable-line no-unused-vars
import NewInfo from "../components/Info/NewInfo"
export default {
components:{
    SingleInfo,
    NewInfo,
    FixInfo
},
data(){
    return{
        dialogFormVisible:false,
        load : {},
        iteminpage:0,
        err :"",
        newInfo:{
            Content: "",
            Title: "",
            username:this.name
        },
        fixForm:{},
        }
},
computed:{
    name(){
        return this.$store.state.UserName
    }
},
methods:{
    closeByChild(value){
            this.allInfo(this)
            this.dialogFormVisible = value
    },
    allInfo(_this){
        let allInfo =   AxiosGet("infos" , "")
        allInfo.then((responseObj)=>{
            _this.load = responseObj.data
            for(var i in _this.load.Data) _this.iteminpage++//eslint-disable-line no-unused-vars
        }).catch((err)=>{
            console.log(err)
            this.$message.error('Error!')
        })
    },
    addNewInfo(newInfo){
        newInfo.username = this.name
        let addNewInfo = InfoAdd('infos',newInfo)
        addNewInfo.then((message)=>{
            this.allInfo(this)
            console.log(message)
            this.load.Data.push(newInfo)
            this.newInfo = {
            Content: "",
            Title: "",
            }
        }).catch(()=>{
           this.$message.error('Error!')
        })
        
    },
    AddtoMyTodo(key){
        let load = InfoTodo(this.name, this.load.Data[key].InfoId)
        load.then(()=>{
            this.allInfo(this)
            this.$message("Add Success!")
        }).catch(()=>{
            this.$message.error("Error!")
        })
    },
    Remove(key){         
        let deleteInfo =  AxiosDelete( "infos/",this.load.Data[key].InfoId)
        deleteInfo.then(()=>{
            this.allInfo(this) 
            this.load.Data.splice(key, 1)
        }).catch((err)=>{
            console.log(err)
        })
    },
    Fix(key){
        this.fixForm = this.load.Data[key]
        this.dialogFormVisible = true;
        
    }

},
mounted(){
    let _this = this
    this.allInfo(_this)
    
}

}
</script>

