<template>
    <el-dialog 
    width=80%
    :close-on-click-modal="false"
    :show-close = "showClose"
    title="Fix Info" 
    :visible.sync="dialogFormVisible">
        <el-form :model="form">
            <el-form-item label="Title" :label-width="formLabelWidth">
                <el-input 
                clearable
                v-model="form.Title" 
                autocomplete="on">
                </el-input>
            </el-form-item>
            <el-form-item label="Content" :label-width="formLabelWidth" >
                <el-input 
                :autosize="{ minRows: 2}"
                v-model="form.Content" 
                autocomplete="on" 
                clearable
                >
                </el-input>
            </el-form-item>
            <el-form-item label="Time" :label-width="formLabelWidth" >
                <el-input 
                v-model="form.StartTime" 
                autocomplete="on">
                </el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="close">取 消</el-button>
            <el-button type="primary" 
            @click="ReSubmit">完 成</el-button>
        </div>
    </el-dialog>
</template>

<script>
import { fixInfo } from "../../../util/submit"
export default {
    props:{
        dialogFormVisible:{ type:Boolean, requires: true},
        form:{ type:Object ,requires:true},
        name:{ type:String, requires:true}
    },
    data(){
        return{
            showClose: false,
            formLabelWidth: '130px',
        }
    },
    methods:{
        ReSubmit(){
            let payload ={};
            payload.username = this.name
            payload.title = this.form.Title
            payload.content = this.form.Content
            
            let fix = fixInfo(this.form.InfoId,payload)
            
            console.log(this.form)
            fix.then(()=>{
                this.$emit("closeByChild", !this.dialogFormVisible)
    
            }).catch(()=>{
                this.$message.error("Error!")
            })
        },
        close(){
            this.$emit("closeByChild", !this.dialogFormVisible)
        },
    }
}
</script>

<style scoped>
.el-dialog__footer {
    padding: 10px 20px 20px;
    text-align: center;
    box-sizing: border-box;
}
</style>>