<template>
<el-main>
    <el-card>
    <div slot="header" class="clearfix">
      <span :title="title">{{title}}</span>
      <el-divider direction="vertical"></el-divider>
      <span :updataTime="updateTime">{{updateTime}}</span>
      <el-button 
      style="float: right; " 
      @click="AddTodo"
      type="span">Add to my Todo</el-button>
      <el-button 
      style="float: right; " 
      type="span"
      @click="Remove">Remove</el-button>
      <el-button 
      style="float: right; " 
      type="span"
      @click="Fix">Fix</el-button>
    </div>
    <div :content="content">
      {{content}}
    </div>
  </el-card>
</el-main>

</template>

<script>
export default {
  props:{ 
      title:{type:String , required:true},
      content:{type:String , required:true},
      updateTime:{type:String ,},
      index:{type:Number, required:true}
    },
    components:{

    },
    data() {
        return{

        }
    },
    methods:{
      Remove(){
      this.$emit('RemoveByChild' , this.index)
      },
      Fix(){
        this.$emit('FixByChild',this.index)
      },
      AddTodo(){
        this.$emit("AddbyChild",this.index)
      }
    }
}
</script>

<style scoped>
.text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
</style>